@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
.G-container
{
    width: 100%;
    height: 100%;
    padding-top: 10vw;
    padding-bottom: 10vw;
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;  
    transform: scale(90%); 
}
.G-left
{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Cuprum', sans-serif;
    text-align: center;
    align-items: center;
    align-content: center;
    justify-content: center;
}
.G-left>h3
{
    font-size: 2vw;
    font-family: 'Denk one';
    font-weight: 200;
    color: rgb(24,24,24);
    
}
.G-left>.list>*
{
    font-size: .9vw;
}
.G-left>.list
{
    width: 50%;
    color: rgb(46, 46, 46);
    font-weight: 100;
    font-size: .5vw;
    font-family: 'Denk one';
    font-weight: 100;
    line-height: 3vw;
    border-top: 1px solid gray;
    display: flex;
    text-align: center;
    align-items: center;
    align-content: center;
    justify-content: center;
}


.G-right
{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
}
.G-right>img
{
    width: 90%;
}



@media screen and (max-width:480px)
{
    .G-container
    {
        width: 100%;
        height: 100%;
        padding-top: 20vw;
        padding-bottom: 20vw;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;   
    }
    .G-left
    {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        font-family: 'Cuprum', sans-serif;
        text-align: center;
        align-items: center;
        align-content: center;
        justify-content: center;
    }
    .G-left>h3
    {
        font-size: 5vw;
        font-family: 'Denk one';
        font-weight: 200;
        color: rgb(175,11,110);
    }
    .G-left>.list>*
    {
        font-size: 3.5vw;
    }
    .G-left>.list
    {
        width: 100%;
        color: rgb(46,46,46);
        font-weight: 100;
        font-size: 3.5vw;
        font-family: 'Denk one';
        font-weight: 100;
        line-height: 10vw;
        border-top: 1px solid gray;
        display: flex;
        text-align: center;
        align-items: center;
        align-content: center;
        justify-content: center;
    }


    .G-right
    {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .G-right>img
    {
        width: 90%;
    }
}