@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&family=Satisfy&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&family=Satisfy&family=Sriracha&display=swap');

.H-container
{
    width: 100%;
    height: 100%;
    padding-top: 2vw;
    padding-bottom: 5vw;
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;   
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 60%;
    color: rgb(24,24,24);
}
.H-left
{
    width: 50%;
    height: 90%;
    display: flex;
    font-family:'Cuprum';
    align-items: center;
    align-content: center;
    justify-content: right;
    transform: scale(85%);
    /* z-index: -1; */
}
.H-left-1
{
    width: 70%;
    height: auto;
    padding: 20px;
    line-height: 0.5vw;
    /* font-family:'Courier New', Courier, monospace; */
    /* font-family: 'Lobster', cursive; */
    text-align: left;
}
.H-left-1>div>h1
{
    font-size: 5vw;
    font-family:'Denk one';
    letter-spacing: 0.5vw;
}
.H-left-1>p
{
    width: 100%;
    height: auto;
    padding: 20px;
    line-height: 2vw;
    font-size: 1vw;
    font-family:'Cuprum';
    color: rgb(206, 200 200);
    letter-spacing: 0.1vw;
    font-weight: 700;
    border: 0.5px solid lightgray;
    border-radius: 5px;
}
.H-left-1>.btns
{
    width: 90%;
    height: auto;
    padding: 10px;
    display: flex;
    justify-content: right;
    gap: 1.5vw;
}
.H-left-1>.btns>button
{
       width: 30%;
       height: auto;
       padding: 0.7vw;
       border: none;
       border-radius: 20px;
       font-size: 1vw;
       font-family: 'Denk one';
       font-weight: 200;
       color: aliceblue;
       letter-spacing: 0.12vw;
       /* border: 0.5px solid gray; */
       background-color: rgb(175, 11, 110);
       cursor: pointer;
}
.H-left-1>.btns>button:hover
{
    transition: all .1s ease-in-out;
    background-color: rgb(175,11,110);
    transform: scale(105%);
    color: aliceblue;
    box-shadow: 0 5px 5px 0 black;
}


.H-right
{
    width: 50%;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(80%);
    /* z-index: -1; */
}
.H-right>:nth-child(2)
{
    display: none;
}
.H-right>:nth-child(1)
{
    width: 60%;
    height: 90%;
    border-radius: 5px;
    display: block;
    /* box-shadow: 0 10px 50px 0 black; */
}
.forms
{
    width: auto;
    height: auto;
    position: fixed;
    margin: 0;
    justify-content: center;
    align-items: center;
    padding: 2.5vw;
    background-color: rgb(245, 245, 245);
    border-radius: .5vw;
}
.forms>button
{
    border-radius: 20vw;
    background-color: rgb(175,11,110);
}
.forms>span
{
    color: red;
    letter-spacing: 0.5vw;
    font-weight: 500;
    position: relative;
    top: 2vw;
}





.H1-container
{
    width: 100%;
    height: 100;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;  
    background-color: white;
    /* transform: scale(80%);
    z-index: -1; */
}
.H1-container>.H1-contain
{
    width: 80%;
    height: 20vw;
    padding: 10vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 8vw;
    padding-top: 5vw;
    padding-bottom: 5vw;
}
.H1-container>.H1-contain>.cards
{
    width: 20%;
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/Home.jpg);
    border-radius: .2vw;
    /* box-shadow: 0 5px 10px 0 rgb(71, 71, 71); */
}
.H1-container>.H1-contain>.cards>img
{
    width: 100%;
    height: 70%;
}
.H1-container>.H1-contain>.cards>p
{
   width: 60%;
   font-size: 1.3vw;
   font-weight: 100;
   padding: 0.5vw;
   line-height: 2vw;
   font-family: 'Cuprum';
   color: rgba(255, 255, 255, 0.774);
}

/* ===============  Responsive  ================ */

@media screen and (max-width:480px)
{
    .H-container
    {
        width: 100%;
        height: 100%;
        padding-top: 0vw;
        display: flex;
        flex-direction: column-reverse;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;   
        background:none;
    }
    .H-left
    {
        width: 100%;
        height: 100%;
        padding-top: 10vw;
        padding-bottom: 20vw;
        position: relative;
        top: -80vw;
        display: flex;
        font-family: 'Cuprum', sans-serif;
        align-items: center;
        align-content: center;
        justify-content: center;
        transform: scale(100%);
    }
    .H-left-1
    {
        width: 100%;
        height: 100%;
        line-height: 0.5vw;
        font-family: 'Denk One', sans-serif;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .H-left-1>h4
    {
        display: none;
    }
    .H-left-1>div>h3,h1
    {
        display: none;
    }
    .H-left-1>div>h1
    {
        font-size: 5vw;
        color: rgb(175,11,110);
        text-shadow: 0 5px 10px black;
        font-family: 'Kaushan Script', cursive;
        letter-spacing: 0.5vw;
    }
    .H-left-1>.ex-data
    {
        display: none;
    }
    .H-left-1>p
    {
        width: 90%;
        height: auto;
        line-height: 7vw;
        font-size: 5vw;
        border: none;
        padding: 0;
        font-family: 'Cuprum', sans-serif;
        color: rgb(252, 250, 250);
        font-weight: 200;
    }
    .H-left-1>.btns
    {
        width: 100%;
        height: auto;
        padding: 10px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        gap: 30vw;
    }
    .H-left-1>.btns>:nth-child(2)
    {
        display: none;
    }
    .H-left-1>.btns>button
    {
        width: 30%;
        height: auto;
        padding: 2.5vw;
        font-size: 4vw;
        cursor: pointer;
        letter-spacing: 0.3vw;
        font-weight: 200;
        font-family: 'Cuprum', sans-serif;
        border: none;
        border-radius: 0.5vw;
        color: aliceblue;
        background-color: rgb(175,11,110);
        box-shadow: 0 10px 10px 0 black;
    }
    .H-left-1>.btns>button:hover
    {
        transition: all .1s ease-in-out;
        background-color: rgb(175,11,110);
        transform: scale(105%);
    }


    .H-right
    {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.6));
        transform: scale(100%);
        z-index: -1;
    }
    .H-right>:nth-child(1)
    {
        display: none;
    }
    .H-right>:nth-child(2)
    {
        width: 100%;
        height: 100%;
        display: block;
        border-radius:0;
        box-shadow: 0 5px 5px 0 black;
        z-index: -1;
    }
    .forms
    {
        width: 70%;
        height: 60%;
        padding-top: 10vw;
        padding-bottom: 10vw;
        position: fixed;
        top: 30vw;
        left: 10vw;
        text-align: center;
        padding: 5vw;
        background-color: rgba(255, 255, 255, 0.979);
        border-radius: .5vw;
        z-index: 1;
    }
   .forms>*
    {
        width: 50%;
        font-size: 3.9vw;
    }


    .H1-container
    {
        display: none;
    }

}