@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
.E-container
{
    width: 100%;
    height: 100%;
    padding-top: 10vw;
    padding-bottom: 10vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;  
    background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/Home.jpg);  
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 3vw;

}
.E-container>h4
{
    font-size: 2vw;
    font-family: 'Denk one';
    font-weight: 300;
    color: rgb(255, 255, 255);
}
.E-container>div
{
    width: 100%;
    height: auto;
    display: flex;
    padding-bottom: 10vw;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 5vw;
    justify-content: center;
    align-items: center;
}
.E-container>div>.card
{
    width: 30%;
    height: auto;
    line-height: 0.5vw;
    text-align: left;
    font-family: 'Denk one';
    color: rgb(151, 150, 149);
    border-left: 1.5vw solid rgb(77, 4, 47);
    background-color:rgba(0, 0, 0, 0.37);
}
.E-container>div>.card>*
{
    margin-left: 2vw;
}
.E-container>div>.card>h3
{
    font-size: 1.5vw;
    font-weight: 100;
    text-decoration:underline;
}
.E-container>div>.card>h5
{
    font-size: 1vw;
    font-weight: 100;
}
.E-container>div>.card>h6
{
    font-size: 0.8vw;
    font-weight: 100;
}
.Sk-container
{
    width: 100%;
    height: 100%;
    padding-top: 10vw;
    padding-bottom: 10vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;  
    background-color: white;
    /* background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/Home.jpg);  
    background-repeat: no-repeat;
    background-size: cover; */
    padding-bottom: 3vw;

}
.Sk-container>h4
{
    color: rgb(24,24,24);
    font-size: 2vw;
    font-family: 'Denk one';
    font-weight: 100;
}
.Sk-container>div
{
    width: 50%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 5vw;
    padding-bottom: 10vw;
    line-height: 0.2vw;
    justify-content: center;
    align-items: center;
}

.Sk-container>div>h3
{
    font-size: 1.5vw;
    font-weight: 200;
    text-decoration:none;
    color: rgb(54, 54, 54);
    font-family: 'Denk one';
}
.Sk-container>.bt
{
    display: none;
}



@media screen and (max-width:480px)
{
    .E-container
    {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;  
        background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/Home.jpg);  
        background-repeat: no-repeat;
        background-size: cover;
        padding-bottom: 3vw;
        background-position: 80%;
    
    }
    .E-container>h4
    {
        font-size: 5vw;
        font-family: 'Denk one';
        font-weight: 300;
        color: rgb(255, 255, 255);
        text-align: left;
    }
    .E-container>div
    {
        width: 100%;
        height: auto;
        padding-top: 10vw;
        padding-bottom: 30vw;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 5vw;
        justify-content: center;
        align-items: center;
    }
    .E-container>div>.card
    {
        width: 80%;
        height: auto;
        line-height: 0.5vw;
        text-align: left;
        font-family: 'Denk one';
        color: rgb(238, 237, 236);
        border-left: 1.5vw solid rgb(17, 0, 8);
        background-color:rgba(0, 0, 0, 0.37);
    }
    .E-container>div>.card>*
    {
        margin-left: 2vw;
    }
    .E-container>div>.card>h3
    {
        font-size: 3.5vw;
        font-weight: 100;
        text-decoration:underline;
    }
    .E-container>div>.card>h5
    {
        font-size: 3vw;
        font-weight: 100;
    }
    .E-container>div>.card>h6
    {
        font-size: 2.8vw;
        font-weight: 100;
    }

    .Sk-container
    {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;  
        /* background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/Home.jpg);  
        background-repeat: no-repeat;
        background-size: cover; */
        padding-bottom: 3vw;
    
    }
    .Sk-container>h4
    {
        font-size: 5vw;
        font-family: 'Denk one';
        font-weight: 300;
        color: rgb(8,81,112);
        text-shadow: 0 1px 2px black;
        display: flex;
        text-align: left;
        align-items: left;
        justify-content: left;
    }
    .Sk-container>div
    {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        /* flex-wrap: wrap;
        justify-content: space-between; */
        gap: 1vw;
        line-height: normal;
        justify-content: left;
        align-items: left;
    }
    .Sk-container>div>h3
    {
        font-size: 3.5vw;
        font-weight: 200;
        text-decoration:none;
        color: rgb(175,11,110);
        font-family: 'Denk one';
    }
    .Sk-container>.bt
    {
        display: block;
        width: 100%;
        height: 10vw;
        display: flex;
        position: relative;
        top: 5vw;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;
        cursor: pointer;
        background-color: rgb(112, 8, 56);
    }
    .Sk-container>.bt>a>p
    {
        font-size: 5vw;
        font-weight: 100;
        display: flex;
        color:whitesmoke;
        position: relative;
        top: 5vw;
        font-family: 'Denk one';
        text-align: center;
        align-items: center;
        justify-content: center;
        align-content: center;
        text-decoration: none;
        
    }
}