@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');

.N-container
{
   width: 100%;
   height: auto;
   display: flex;
   padding-top: 1vw;
   /* padding-bottom: 1vw; */
   flex-direction: row;
   font-family: 'Cuprum', sans-serif;
   position: sticky;
   top: 0;
   background-color: white;
   z-index: 100;
   /* background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/Home.jpg);
   background-repeat: no-repeat;
   background-size: cover;
   box-shadow: 0 5px 5px 0 rgb(206, 202, 202); */
}
.N-left
{
    width: 50%;
    height: auto;
}
.N-left>img
{
    width: 6vw;
    height: 3vw;
    position: relative;
    left: 12vw;
}
.N-right
{
    width: 50%;
    height: auto;
}
.N1-container
{
    display: none;
}


@media screen and (max-width:480px)
{
    .N-container
    {
        display: none;
    }
    .N1-container
    {
        display: block;
       
    }
    .N-left
    {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: left;
        text-align: left;
        font-size: 4vw;
        font-weight: 700;
        font-family: 'Cuprum';
        line-height: .0001vw;
        
    }
    .N-left>img
    {
        width: 25vw;
        height: 15vw;
        display: flex;
        justify-content: left;
        position: relative;
        left: 5vw;
    }
    .listIcon
    {
        width: 11vw;
        height: 11vw;
        display: flex;
        position: fixed;
        top: 3vw;
        left: 85vw;
        z-index: 100;
        font-size: 0.8vw;
        font-family: fantasy;
        font-weight: 900;
        justify-content: center;
        align-items: center;
        background-color: rgb(175,11,110);
        border-radius: 100%;
        color: aliceblue;
    }
    .backTop
    {
        width: 11vw;
        height: 11vw;
        display: flex;
        font-size: 0.8vw;
        font-family: fantasy;
        font-weight: 900;
        position: fixed;
        top: 90%;
        left: 85vw;
        z-index: 100;
        justify-content: center;
        align-items: center;
        background-color: rgb(175,11,110);
        border-radius: 100%;
        color: aliceblue;
    }
    .menuList
    {
        width: 70%;
        height: 90%;
        /* padding: 4vw; */
        /* line-height: 10vw; */
        position: fixed;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        right: 0vw;
        top: 14vw;
        z-index: 100;
        background-color: white;
    }
    .menuList>:nth-child(1)
    {
        width: 100%;
        height: 30%;
        display: flex;
        padding-top: 2vw;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;
        line-height: 0.1vw;
        background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.6)),url(../../img/menuB.jpg);
        background-repeat: no-repeat;
        background-size: contain;
    }
    .menuList>:nth-child(1)>p
    {
        color: aliceblue;
        display: flex;
        flex-direction: row;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;
        gap: 2vw;
        font-size: 2.5vw;
        letter-spacing: 0.5vw;
    }
    .menuList>:nth-child(2)
    {
        width: 100%;
        height: 70%;
        /* padding-left: 30vw; */
        padding-top: 5vw;
        line-height: 10vw;
    }
    .menuList>:nth-child(2)>p
    {
        display: flex;
        flex-direction: row;
        justify-content: left;
        text-align: center;
        align-items: center;
        align-content: center;
        gap: 5vw;
        color: rgb(175,11,110);
        border-bottom: 0.5px solid lightgray;
    }
    .menuList>:nth-child(3)
    {
        width: 80%;
        height: auto;
        display: flex;
        flex-direction: column;
        padding: 1vw;
        justify-content: center;
        align-items: center;
        text-align: center;
        align-content: center;
        /* background-color: aqua; */
        color: darkgrey;
        font-size: 3vw;
        font-family: sans-serif;
    }

    .menuList>div>img
    {
        width: 35vw;
        height: 35vw;
        border-radius: 100%;
    }
}