@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
.S-container
{
    width: 100%;
    height: 100%;
    padding-top: 1vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;
    /* background-color:white;   */
    background:linear-gradient(rgba(0, 0, 0,0.7),rgba(0,0,0,0.6)),url(../../img/service.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
}
.S-container>h4
{
    font-size: 2vw;
    font-family: 'Denk one';
    font-weight: 300;
    color: rgb(194, 194, 194);
}
.S-container>p
{
    font-size: 1vw;
    font-family: 'Denk one';
    font-weight: 100;
    letter-spacing: 0.1vw;
    color: rgb(194, 194, 194);
    /* color: rgba(255, 255, 255, 0.788); */
    /* color: rgb(54, 54, 54); */
    /* text-shadow: 0 1px 2px black; */
    text-align: center;
}
.S-container>div
{
    width: 98%;
    height: auto;
    transform: scale(80%);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 2vw;
    padding-top: 2vw;
    padding-bottom: 2vw;
    justify-content: center;
    align-items: center;
    /* background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.6)),url(../../img/service.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%; */
    
}
.S-container>div>.card>img
{
    width: 8vw;
    height: 8vw;
    color: azure;
}
.S-container>div>.card>p
{
    width: 100%;
    height: auto;
    border-radius: 5px;
    line-height: 1.5vw;
    overflow-wrap: break-word;
    font-size: 1vw;
    font-family: 'Cuprum';
    font-weight: 300;
    letter-spacing: 0.1vw;
    color: rgba(255, 255, 255, 0.932);
    text-shadow: 0 1px 2px black;
    text-align: center;
}
.S-container>div>.card>h4
{
    /* color: rgb(175,11,110); */
    color: rgb(252, 201, 222);
    font-family: 'Denk one';
    font-size: 1.2vw;
    font-weight: 100;
    letter-spacing: .2vw;

}
.S-container>div>.card
{
    width: 20%;
    height: auto;
    padding: 10px;
    line-height: 0.01vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    /* box-shadow: 0 10px 10px 0 black; */
    font-size: 1vw;
    font-family: 'Cuprum';
}
.Ac-container>.btns>button
    {
       width: 20%;
       height: auto;
       padding: .8vw;
       border: none;
       border-radius: 20px;
       font-size: 1vw;
       font-family: 'Denk one';
       font-weight: 200;
       color: beige;
       letter-spacing: 0.12vw;
       background-color: rgb(175,11,110);
       box-shadow: none;
       cursor: pointer;
    }


@media screen and (max-width:480px)
{
    .S-container
{
    width: 100%;
    height: 100%;
    padding-top: 3vw;
    justify-content: center;
    align-items: center;
    align-content: center;  
}
.S-container>h4
{
    font-size: 5vw;
    font-family: 'Denk one';
    font-weight: 200;
    color: whitesmoke;
    text-shadow: 0 1px 2px black;
    text-align: center;
}
.S-container>p
{
    font-size: 3vw;
    font-family: 'Denk one';
    font-weight: 100;
    letter-spacing: 0.3vw;
    color: rgba(255, 255, 255, 0.822);
    text-shadow: 0 1px 2px black;
    text-align: center;
}
.S-container>div
{
    width: 100%;
    height: auto;
    padding-left: 10vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10vw;
    justify-content: center;
    align-items: center;
    /* background:linear-gradient(rgba(85, 183, 212, 0.7),rgba(1, 63, 92, 0.6)),url(../../img/service.jpg); */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 60%; 
}
.S-container>div>.card>img
{
    /* display: none; */
    width: 16vw;
    height: 16vw;
}
.S-container>div>.card>p
{
    display: none;
}
.S-container>div>.card
{
    width: 100%;
    height: auto;
    padding: 2px;
    display: flex;
    flex-direction: row;
    gap:5vw;
    text-align: left;
    align-items: left;
    justify-content: left;
}
.S-container>div>.card>h4
{
    font-size: 5vw;
    font-family: 'Denk one';
    font-weight: 100;
    letter-spacing: 0.3vw;
    color: rgba(255, 255, 255, 0.822);
    text-shadow: 0 1px 2px black;
    display: flex;
    justify-content: left;
    text-align: left;
}
.Ac-container>.btns>button
    {
       width: 50%;
       height: auto;
       padding: 4vw;
       border: none;
       border-radius: 20vw;
       font-size: 4vw;
       font-family: 'Denk one';
       font-weight: 200;
       color: beige;
       letter-spacing: 0.12vw;
       background-color: rgb(175,11,110);
       box-shadow: none;
       cursor: pointer;
    }
}