@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
.W-container
{
    width: 100%;
    height: 100%;
    padding-top: 5vw;
    padding-bottom: 10vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: rgba(243, 236, 205, 0.384);  
}
.W-container>h4
{
    font-size: 2vw;
    font-family: 'Denk one';
    font-weight: 200;
    color: rgb(8,81,112);
}
.W-container>div
{
    transform: scale(70%);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 5vw;
    justify-content: center;
    align-items: center;
}
.W-container>div>.card
{
    width: 40%;
    height: 80%;
    display: flex;
    flex-direction: column;
    line-height: 0.5vw;
    text-align: center;
    align-items: center;
    align-content: center;
    justify-content: center;
    font-family: 'Denk one';
    background:url(../../img/work.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    color: transparent;
}
.W-container>div>.card:hover
{
   color: antiquewhite;
   background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/work.jpg);
   background-repeat: no-repeat;
   background-size: cover;
}
.W-container>div>.card>*
{
    margin-left: 2vw;
}
.W-container>div>.card>h3
{
    font-size: 1.5vw;
    font-weight: 100;
    text-decoration:underline;
}
.W-container>div>.card>h5
{
    font-size: 1vw;
    font-weight: 100;
}
.W-container>div>.card>h6
{
    font-size: 0.8vw;
    font-weight: 100;
}


@media screen and (max-width:480px)
{
    .W-container
    {
        width: 100%;
        height: 100%;
        padding-top: 5vw;
        padding-bottom: 10vw;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;
        background-color: rgba(243, 236, 205, 0.384);
    }
    .W-container>h4
    {
        font-size: 5vw;
        font-family: 'Denk one';
        font-weight: 200;
        color: rgb(8,81,112);
    }
    .W-container>div
    {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 5vw;
        justify-content: center;
        align-items: center;
    }
    .W-container>div>.card
    {
        width: 80%;
        height: 100%;
        display: flex;
        flex-direction: column;
        line-height: 0.5vw;
        text-align: center;
        align-items: center;
        align-content: center;
        justify-content: center;
        font-family: 'Denk one';
        background:url(../../img/work.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        color: transparent;
    }
    .W-container>div>.card:hover
    {
    color: antiquewhite;
    background:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/work.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    }
    .W-container>div>.card>*
    {
        margin-left: 2vw;
    }
    .W-container>div>.card>h3
    {
        font-size: 3.5vw;
        font-weight: 100;
        text-decoration:underline;
    }
    .W-container>div>.card>h5
    {
        font-size: 3vw;
        font-weight: 100;
    }
    .W-container>div>.card>h6
    {
        font-size: 2.8vw;
        font-weight: 100;
    }
    
}