@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
.A-container
{
    width: 100%;
    height: 100%;
    padding-top: 1vw;
    padding-bottom: 1vw;
    text-align: center;
    display: flex;
    justify-content: right;
    align-items: center;
    align-content: center;   
    background-image: url(../../img/santh.jpg); 
    background: linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url(../../img/santh.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}
.A-container>.main
{
    width: 40%;
    height: auto;
    padding: 10px;
    padding-right: 5rem;
    text-align: left;
    transform: scale(85%);
}
.A-container>.main>h4
{
    font-size: 2vw;
    font-family: 'Denk one';
    font-weight: 200;
    color: aliceblue;
}
.A-container>.main>p
{
    width: 80%;
    height: auto;
    border: 0.5px solid rgba(124, 123, 123, 0.39);
    border-radius: 5px;
    padding: 20px;
    line-height: 2vw;
    text-align: left;
    letter-spacing: 0.1vw;
    color: azure;
    font-size: 1vw;
    font-family: 'Cuprum';
}
.A-container>.main>.list
{
    color: rgb(236, 230, 234);
    font-size: 1vw;
    font-family: 'Cuprum';
    line-height: 3vw;
    text-decoration: underline;
    text-shadow: 0 5px 5px black;
    /* position: relative;
    left: -3vw;
    z-index: 1; */
}



.Ac-container
{
  width: 100%;
  height: auto;
  padding-top: 10vw;
  padding-bottom: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  background-color: rgb(249, 251, 252);
}
.Ac-container>.btns
{
  width: 50%;
  height: auto;
  line-height: 2vw;
}
.Ac-container>.btns>h1
{
    color: rgb(54, 54, 54);
    font-family: 'Cuprum';
    font-size: 1.5vw;
    line-height: 2.5vw;
    font-weight: 100;
}
.Ac-container>.btns>button
{
   width: 10%;
   height: auto;
   padding: 0.8vw;
   border: none;
   border-radius: 2px;
   font-size: 1vw;
   font-family: 'Denk one';
   font-weight: 200;
   color: beige;
   background-color: rgb(175,11,110);
   box-shadow: 0 10px 10px 0 black;
   cursor: pointer;
}

@media screen and (max-width:480px)
{
    .A-container
    {
        width: 100%;
        height: 100%;
        padding-top: 0vw;
        padding-bottom: 0vw;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;   
        background: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.7)),url(../../img/santh.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 12%;
    }
    .A-container>.main
    {
        width: 100%;
        height: auto;
        position: relative;
        top: -82vw;
        padding: 10px;
        line-height: 1vw;
        text-align: center;
        align-items: center;
        transform: scale(100%);
    }
    .A-container>.main>h4
    {
        font-size: 5vw;
        font-family: 'Denk one';
        font-weight: 100;
        color: rgb(11,113,135);
        /* text-shadow: 0 1px 2px rgba(0, 0, 0, 0.671); */
        text-align: left;
    }
    .A-container>.main>p
    {
        width: 90%;
        height: auto;
        border: 0.5px solid rgba(124, 123, 123, 0.39);
        border-radius: 5px;
        padding: 4vw;
        line-height: 6.5vw;
        text-align: left;
        color: rgb(68, 71, 71);
        letter-spacing: 0;
        font-size: 3.5vw;
        font-family: 'Cuprum';
        font-weight: 500;
    }
    .A-container>.main>.list
    {
        color: rgb(250, 248, 249);
        font-size: 4vw;
        font-family: 'Cuprum';
        line-height: 10vw;
        position: relative;
        top: 85vw;
        left: 45vw;
        text-decoration: underline;
        text-shadow: 0 5px 5px black;
        /* position: relative;
        left: -3vw;
        z-index: 1; */
    }
    
    
    
    .Ac-container
    {
      width: 100%;
      height: 30%;
      padding-top: 20vw;
      padding-bottom: 20vw;
      display: flex;
      justify-content: center;
      align-items: center;
      align-content: center;
      text-align: center;
      background-color: rgb(255, 255, 255);
    }
    .Ac-container>.btns
    {
      width: 80%;
      height: auto;
      line-height: 5vw;
    }
    .Ac-container>.btns>h1
    {
        display: block;
        /* color: beige; */
        color: rgb(54, 54, 54);
        font-family: 'Denk one';
        font-size: 4vw;
        font-weight: 100;
        line-height: 8vw;
        /* text-shadow: 0 5px 5px black; */
    }
    .Ac-container>.btns>button
    {
       width: 40%;
       height: auto;
       padding: 2vw;
       border: none;
       border-radius: 2px;
       font-size: 3.5vw;
       font-family: 'Denk one';
       font-weight: 200;
       color: beige;
       letter-spacing: 0.3vw;
       background:url(../../img/btnB.jpg);
       box-shadow: 0 10px 10px 0 black;
       cursor: pointer;
    }
        
}