@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
.C-container
{
    width: 100%;
    height: 100%;
    padding-top: 1vw;
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;  
}
.C-left
{
    width: 100%;
    height: 30vw;
    font-family: 'Cuprum', sans-serif;
    display: flex;
    flex-direction: row;
    gap: 10vw;
    text-align: center;
    align-items: center;
    align-content: center;
    justify-content: center;
    background-color: rgb(8,81,112); 
}
.C-left>img
{
    width: 40%;
    height: 80%;
    /* box-shadow: 0 10px 10px 0 rgb(145, 143, 143); */
}
.C-left>.C-content
{
    display: block;
    text-align: left;
    line-height: 3vw;
    color: rgb(218, 220, 221);
}
.C-left>.C-content>h2
{
    font-weight: 100;
    font-size: 2vw;
}
.C-left>.C-content>p
    {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        text-align: left;
        justify-content: left;
        gap: 1vw;
        font-size: 1vw;
        letter-spacing: .1vw;
    }
.C-left>.C-content>div>*
{
    font-size: 3vw;
}
.C-left>.C-content>div
{
    width: 100%;
    font-size: 5vw;
    display: flex;
    flex-direction: row;
    gap: 2vw;
}
.C-left>.C-content>span
{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    align-items: center;
    line-height: 0;
    gap: 1vw;
    font-size: 1vw;
    color: rgba(240, 248, 255, 0.329);
}



.C-right
{
    width: 100%;
    display: flex;
    justify-content: center;
    /* background-color: white; */
}
.C-right>div
{
    width: 100%;
    padding: 15vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    gap: 3vw;
}
.C-right>div>:nth-child(1)
{
    width: 50%;
}
.C-right>div>:nth-child(2)>button
{
    border-radius: 20vw;
    background-color: rgb(175,11,110);
}
.C-right>div>:nth-child(2)
{
    width: 50%;
}


@media screen and (max-width:480px)
{
    .C-container
    {
        width: 100%;
        height: 100%;
        padding-top: 1vw;
        display: flex;
        flex-direction: column-reverse;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;  
        background-color:white; 
    }
    .C-left
    {
        width: 100%;
        height: 100%;
        padding: 3vw;
        display: flex;
        flex-direction: column;
        font-family: 'Cuprum', sans-serif;
        text-align: center;
        align-items: center;
        align-content: center;
        justify-content: center;
    }
    .C-left>img
    {
        width: 90%;
        height: 20%;
    }
    .C-left>.C-content
{
    display: block;
    text-align: left;
    line-height: 3vw;
    color: rgb(218, 220, 221);
}
.C-left>.C-content>h2
{
    font-weight: 100;
    font-size: 4vw;
}
.C-left>.C-content>p
    {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        text-align: left;
        justify-content: left;
        gap: 1vw;
        font-size: 4vw;
        letter-spacing: .1vw;
    }
.C-left>.C-content>div>*
{
    font-size: 8vw;
}
.C-left>.C-content>div
{
    width: 100%;
    font-size: 8vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5vw;
}
.C-left>.C-content>span
{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    align-items: center;
    line-height: 0;
    gap: 1vw;
    font-size: 3vw;
    color: rgba(240, 248, 255, 0.329);
}


    .C-right
    {
        width: 100%;
        padding: 0px;
        display: flex;
        justify-content: center;
        /* background-color: rgb(138, 14, 14); */
    }
    .C-right>div
    {
        width: 100%;
        background: transparent;
        display: flex;
        flex-direction: column;
        color: aliceblue;
        border-radius: 5px;
    }
    .C-right>div>:nth-child(1)
    {
        width: 100%;
    }
    .C-right>div>:nth-child(2)
    {
        width: 100%;
    }

}