@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum&family=Denk+One&family=Kaushan+Script&family=Lobster&family=Roboto:wght@900&display=swap');
.T-container
{
    width: 100%;
    height: 100%;
    padding-top: 5vw;
    padding-bottom: 10vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.T-container>h4
{
    font-size: 2vw;
    font-family: 'Denk one';
    font-weight: 200;
    color: rgb(8,81,112);
}
.T-container>div
{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 5vw;
    justify-content: center;
    align-items: center;
}
.T-container>div>.card
{
    width: 70%;
    height: 100%;
    padding: 2vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    align-content: center;
    justify-content: center;
    box-shadow: 0 10px 10px 0 rgb(160, 159, 159);
   
}
.T-container>div>.card>img
{
   width: 10vw;
   height: 10vw;
   border-radius: 100%;  
   box-shadow: 0 10px 10px 0 black;  
}
.T-container>div>.card>p
{
   width: 50%;
   line-height: 2vw;
   font-size: 1.2vw;
   font-weight: 300;
   font-family: 'Cuprum';
   text-align: center;
   color: black;
}





@media screen and (max-width:480px)
{
    .T-container
    {
        width: 100%;
        height: 100%;
        padding-top: 5vw;
        padding-bottom: 10vw;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        align-content: center;
        /* background-color: rgb(243, 236, 205);   */
    }
    .T-container>h4
    {
        font-size: 5vw;
        font-family: 'Denk one';
        font-weight: 200;
        color: rgb(8,81,112);
    }
    .T-container>div
    {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 5vw;
        justify-content: center;
        align-items: center;
    }
    .T-container>div>.card
    {
        width: 70%;
        height: 100%;
        display: flex;
        border-radius: 5px;
        flex-direction: column;
        line-height: 0.5vw;
        text-align: center;
        align-items: center;
        align-content: center;
        justify-content: center;
        font-family: 'Denk one';
        background-repeat: no-repeat;
        background-size: cover;
        color: transparent;
    }
    .T-container>div>.card>img
    {
    width: 30vw;
    height: 30vw;
    border-radius: 100%;  
    box-shadow: 0 10px 10px 0 black;  
    }
    .T-container>div>.card>p
    {
    width: 80%;
    height: 100%;
    line-height: 5vw;
    font-size: 3.2vw;
    font-weight: 300;
    font-family: 'Cuprum';
    text-align: center;
    color: rgb(139, 137, 137);
    }

       
}